@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif ,-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.background-screen {
  z-index: -999;
  background: url(/pagina-giport/static/media/imagen-1.0026f8c3.jpg) no-repeat center center fixed;
  background-size: cover;
  height: 100vh;
  width: 100% ;
}

.header {
  background-color: #4747479d;
  height: 100vh;
  width: 100% ;
}

.barra {
  background-color: #fac767;
  width: 100%;
  height: 60px;
  box-shadow: 0px 4px 2px -2px rgba(0,0,0,0.75);
}

.logo-space {
  background-color: #fac767;
  width: 140px;
  height: 120px;
  margin: auto;
  border-radius: 50%;
  text-align: center;
  box-shadow: 0px 4px 2px -2px rgba(0,0,0,0.75);
}

.logo {
  display: block;
  height: 90px;
  margin: auto;
  padding-top: 10px;
}

.home-info {
  color: #f1f1f1;
  text-align: center;
  /* font-size: large; */
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.home-info h1{
  font-size: 50px;
}

.home-info p{
  font-size: 20px;
}

@media screen and (max-width: 820px)  {

  .home-info h1{
    font-size: 40px;
  }
  
  .home-info p{
    font-size: 18px;
  }
}

@media screen and (max-width: 735px)  {
  .home-info {
    margin-top: 30px;
    padding-top: 10px;
  }

  .home-info h1{
    font-size: 35px;
  }
  
  .home-info p{
    font-size: 18px;
  }
}

@media screen and (max-width: 555px)  {
  .home-info {
    margin-top: 40px;
    padding-top: 10px;
  }

  .home-info h1{
    font-size: 32px;
  }
  
  .home-info p{
    font-size: 16px;
  }
}

@media screen and (max-width: 450px)  {
  .home-info {
    margin-top: 50px;
    padding-top: 10px;
  }


  .home-info h1{
    font-size: 25px;
  }
  
  .home-info p{
    font-size: 15px;
  }
}

@media screen and (max-width: 350px)  {

  .home-info h1{
    font-size: 20px;
  }
  
  .home-info p{
    font-size: 14px;
  }
}
.content {
  /* text-align: center; */
  margin: 20px auto;

}

